'use client';

import { useState, useEffect } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import Link from 'next/link';
import { useAuth } from '@/contexts/AuthContext';
import {
  HomeIcon,
  UserIcon,
  DocumentDuplicateIcon,
  BanknotesIcon,
  ChartBarIcon,
  Cog8ToothIcon,
  ArrowLeftOnRectangleIcon,
  BellIcon,
  ChevronDownIcon,
  UsersIcon,
  WrenchScrewdriverIcon,
  RocketLaunchIcon
} from '@heroicons/react/24/outline';

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const { user, logout, isAuthenticated, loading } = useAuth();
  const router = useRouter();
  const pathname = usePathname();
  const [isOpen, setIsOpen] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(false);

  useEffect(() => {
    // إذا انتهى التحميل ولم يكن المستخدم مسجلاً، يتم توجيهه إلى صفحة تسجيل الدخول
    if (!loading && !isAuthenticated) {
      router.push('/login');
    }
  }, [loading, isAuthenticated, router]);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  // إنشاء قائمة بالروابط المتاحة بناءً على نوع المستخدم
  const getNavLinks = () => {
    const commonLinks = [
      { name: 'الرئيسية', href: '/dashboard', icon: HomeIcon },
      { name: 'الملف الشخصي', href: '/dashboard/profile', icon: UserIcon },
    ];

    // روابط خاصة بمزودي الخدمات
    const providerLinks = [
      { name: 'إدارة الخدمات', href: '/dashboard/services', icon: WrenchScrewdriverIcon },
      { name: 'طلبات الخدمة', href: '/dashboard/requests', icon: DocumentDuplicateIcon },
      { name: 'الأرباح والمدفوعات', href: '/dashboard/earnings', icon: BanknotesIcon },
      { name: 'الإحصائيات', href: '/dashboard/statistics', icon: ChartBarIcon },
    ];

    // روابط خاصة بالعملاء
    const clientLinks = [
      { name: 'طلباتي', href: '/dashboard/my-requests', icon: DocumentDuplicateIcon },
      { name: 'المدفوعات', href: '/dashboard/payments', icon: BanknotesIcon },
    ];

    // روابط خاصة بالمسؤولين
    const adminLinks = [
      { name: 'إدارة المستخدمين', href: '/dashboard/admin', icon: UsersIcon },
      { name: 'إدارة المحتوى', href: '/dashboard/admin/content', icon: DocumentDuplicateIcon },
      { name: 'إحصائيات المنصة', href: '/dashboard/admin/stats', icon: ChartBarIcon },
    ];

    // تحديد نوع الروابط بناءً على نوع المستخدم
    let roleSpecificLinks = [];
    if (user?.role === 'provider') {
      roleSpecificLinks = providerLinks;
    } else if (user?.role === 'admin') {
      roleSpecificLinks = adminLinks;
    } else {
      roleSpecificLinks = clientLinks;
    }

    const allLinks = [...commonLinks, ...roleSpecificLinks];
    
    // إضافة إعدادات وتسجيل الخروج في النهاية
    allLinks.push({ name: 'الإعدادات', href: '/dashboard/settings', icon: Cog8ToothIcon });
    
    return allLinks;
  };

  const navLinks = getNavLinks();

  return (
    <div className="min-h-screen bg-gray-100">
      {/* شريط التنقل العلوي */}
      <nav className="bg-white shadow-sm py-2 fixed top-0 left-0 right-0 z-10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-14">
            <div className="flex items-center">
              {/* زر التحكم بالشريط الجانبي على الأجهزة الصغيرة */}
              <button
                onClick={() => setIsOpen(!isOpen)}
                className="md:hidden p-2 rounded-md text-gray-500 hover:text-gray-600 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                <svg className="h-6 w-6" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h7" />
                </svg>
              </button>
              
              {/* شعار التطبيق */}
              <Link href="/" className="ml-8 flex-shrink-0 flex items-center">
                <h1 className="text-2xl font-bold text-indigo-600">عرب<span className="text-green-500">AI</span></h1>
              </Link>
            </div>

            <div className="flex items-center">
              {/* زر الإشعارات */}
              <button className="p-2 rounded-full text-gray-500 hover:text-gray-600 hover:bg-gray-100">
                <BellIcon className="h-6 w-6" />
              </button>
              
              {/* صورة المستخدم والقائمة المنسدلة */}
              <div className="mr-4 relative">
                <div>
                  <button
                    onClick={() => setDropdownOpen(!dropdownOpen)}
                    className="flex items-center space-x-3 space-x-reverse focus:outline-none"
                  >
                    <img
                      className="h-8 w-8 rounded-full object-cover"
                      src={user?.profileImage || "https://randomuser.me/api/portraits/men/1.jpg"}
                      alt="صورة المستخدم"
                    />
                    <div className="hidden md:flex md:items-center">
                      <span className="text-sm text-gray-700 ml-1">{user?.name || 'مستخدم النظام'}</span>
                      <ChevronDownIcon className="h-4 w-4 text-gray-500" />
                    </div>
                  </button>
                </div>
                
                {dropdownOpen && (
                  <div className="origin-top-right absolute left-0 mt-2 w-48 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5 py-1">
                    <Link
                      href="/dashboard/profile"
                      className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                      onClick={() => setDropdownOpen(false)}
                    >
                      الملف الشخصي
                    </Link>
                    <Link
                      href="/dashboard/settings"
                      className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                      onClick={() => setDropdownOpen(false)}
                    >
                      الإعدادات
                    </Link>
                    <button
                      onClick={() => {
                        setDropdownOpen(false);
                        logout();
                        router.push('/');
                      }}
                      className="block w-full text-right px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                    >
                      تسجيل الخروج
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </nav>

      {/* الشريط الجانبي والمحتوى */}
      <div className="flex pt-14">
        {/* الشريط الجانبي */}
        <div
          className={`fixed inset-y-0 pt-14 left-0 z-50 transform ${
            isOpen ? 'translate-x-0' : '-translate-x-full'
          } md:translate-x-0 transition-transform duration-300 ease-in-out bg-white shadow-md md:static md:w-64 md:min-h-screen`}
        >
          <div className="h-full overflow-y-auto">
            <nav className="px-3 py-4">
              <div className="space-y-1">
                {navLinks.map((item) => (
                  <Link
                    key={item.name}
                    href={item.href}
                    className={`group flex items-center px-2 py-2 text-sm font-medium rounded-md ${
                      pathname === item.href
                        ? 'bg-indigo-50 text-indigo-600'
                        : 'text-gray-700 hover:text-indigo-600 hover:bg-indigo-50'
                    }`}
                    onClick={() => setIsOpen(false)}
                  >
                    <item.icon
                      className={`ml-3 flex-shrink-0 h-5 w-5 ${
                        pathname === item.href ? 'text-indigo-600' : 'text-gray-500 group-hover:text-indigo-600'
                      }`}
                      aria-hidden="true"
                    />
                    {item.name}
                  </Link>
                ))}
                <button
                  onClick={() => {
                    logout();
                    router.push('/');
                  }}
                  className="w-full group flex items-center px-2 py-2 text-sm font-medium rounded-md text-red-600 hover:bg-red-50"
                >
                  <ArrowLeftOnRectangleIcon className="ml-3 flex-shrink-0 h-5 w-5 text-red-600" aria-hidden="true" />
                  تسجيل الخروج
                </button>
              </div>
              <div className="mt-10 pt-6 border-t border-gray-200">
                <Link
                  href="/"
                  className="group flex items-center px-2 py-2 text-sm font-medium rounded-md text-indigo-600 hover:bg-indigo-50"
                  onClick={() => setIsOpen(false)}
                >
                  <RocketLaunchIcon className="ml-3 flex-shrink-0 h-5 w-5 text-indigo-600" aria-hidden="true" />
                  الرجوع للموقع
                </Link>
              </div>
            </nav>
          </div>
        </div>

        {/* المحتوى الرئيسي */}
        <div className="flex-1 overflow-auto p-4 md:p-6 md:pr-8">
          {children}
        </div>
      </div>
    </div>
  );
} 